import { mergeStyles } from '@uifabric/merge-styles';
import { css, IStyleFunctionOrObject } from '@uifabric/utilities';
import { TranslationFunction } from 'i18next';
import {
  IModalStyleProps,
  IModalStyles
} from 'office-ui-fabric-react/lib/components/Modal';
import { Label } from 'office-ui-fabric-react/lib/Label';
import { Modal } from 'office-ui-fabric-react/lib/Modal';
import * as React from 'react';
import { IGraphObjectInfo } from '../../../models/graph';
import {
  ConnectedContentSearchObjs,
  ContentSearchObjsType
} from '../ContentSearchObjects';

const modalPgStyles = require('./ModalSearchObjects.scoped.scss');

const STYLES_MODAL_SEARCH_OBJ: IStyleFunctionOrObject<
  IModalStyleProps,
  IModalStyles
> = {
  main: mergeStyles({
    selectors: {
      ['@media (min-width: 1024px)']: {
        left: '5.3vw',
        top: '53px',
        height: 'calc(100vh - 205px)',
        width: '86vw'
      }
    }
  })
};

interface IModalSearchObjectsProps {
  showModal: boolean;
  selectObject: (obj?: IGraphObjectInfo) => void;
  t: TranslationFunction;
}

class ModalSearchObjects extends React.Component<IModalSearchObjectsProps, {}> {
  public render(): JSX.Element {
    return (
      <Modal
        titleAriaId={this.props.t('Search Object')}
        isOpen={this.props.showModal}
        styles={STYLES_MODAL_SEARCH_OBJ}
        onDismiss={this._closeModal}
        isBlocking={false}
        containerClassName="ms-modalExample-container"
      >
        <Label className={css(modalPgStyles.modalTitle)}>Search Object</Label>
        <ConnectedContentSearchObjs
          selectObject={this._selectObject}
          contentSearchObjsType={ContentSearchObjsType.ssm}
        />
      </Modal>
    );
  }

  private _selectObject = (obj?: IGraphObjectInfo) => {
    this.props.selectObject(obj);
  }

  private _closeModal = (): void => {
    this._selectObject();
  }
}
export default ModalSearchObjects;



