import { IContextualMenuItem } from 'office-ui-fabric-react/lib/components/ContextualMenu';
import { FormPanelType } from '../RoleDefinition/RoleDefinitionPanel/RoleDefinitionPanel.type';
// import { TranslationFunction } from 'react-i18next';

// Getting initial CommandBar configured
export const getRoleAssignmentCommandBarItems = (
  refresh: () => void,
  setShowPanel: (showPanel: boolean, panelType: FormPanelType) => void,
  deleteRecords: () => void,
  isDeleteBtnDisabled = true
): IContextualMenuItem[] => {
  return [
    {
      key: 'addNewRoleAssignment',
      name: 'New',
      iconProps: {
        iconName: 'Add'
      },
      ariaLabel: 'Add Role Assignment',
      onClick: () => setShowPanel(true, FormPanelType.new)
    },
    {
      key: 'refresh',
      name: 'Refresh',
      iconProps: {
        iconName: 'Refresh'
      },
      onClick: () => refresh()
    },
    {
      key: 'delete',
      name: 'Delete',
      disabled: isDeleteBtnDisabled,
      iconProps: {
        iconName: 'Delete'
      },
      onClick: () => deleteRecords()
    }
  ];
};



