import { TranslationFunction } from 'i18next';
import { css, DefaultButton, PrimaryButton } from 'office-ui-fabric-react';
import Dialog, {
  DialogFooter,
  DialogType
} from 'office-ui-fabric-react/lib/Dialog';
import {
  IWithResponsiveModeState,
  withResponsiveMode
} from 'office-ui-fabric-react/lib/utilities/decorators/withResponsiveMode';
import * as React from 'react';
import { dialogClass } from './ConfirmationDialogStyle';

interface IDeleteConfirmationDialogProps extends IWithResponsiveModeState {
  isHidden: boolean;
  cancel: () => void;
  confirm: () => void;
  title: string;
  subText: string;
  confirmBtnText: string;
  cancelBtnTxt: string;
  t: TranslationFunction;
}
@withResponsiveMode
export class ConfirmationDialog extends React.Component<
  IDeleteConfirmationDialogProps
> {
  public render() {
    const className =
      this.props.responsiveMode! > 2 ? dialogClass.large : dialogClass.small;
    const {
      cancel,
      cancelBtnTxt,
      isHidden,
      title,
      subText,
      confirmBtnText,
      confirm,
      t
    } = this.props;
    return (
      <Dialog
        hidden={isHidden}
        onDismiss={cancel}
        dialogContentProps={{
          type: DialogType.close,
          title: title,
          subText: subText
        }}
        modalProps={{
          className: css(className),
          titleAriaId: t('Delete Record(s)'),
          subtitleAriaId: t('Selected Record(s) will be deleted.'),
          isBlocking: true
        }}
      >
        <DialogFooter>
          <PrimaryButton onClick={confirm} text={confirmBtnText} />
          <DefaultButton onClick={cancel} text={cancelBtnTxt} />
        </DialogFooter>
      </Dialog>
    );
  }
}



