import { css } from '@uifabric/utilities';
import { CommandBar } from 'office-ui-fabric-react/lib/CommandBar';
import { IconButton } from 'office-ui-fabric-react/lib/components/Button';
import { Dropdown, IDropdownOption } from 'office-ui-fabric-react/lib/Dropdown';
import { TextField } from 'office-ui-fabric-react/lib/TextField';
import * as React from 'react';
import { isNullOrUndefined } from 'util';
import {
  EmptyRoleAssignmentQueryParams,
  IRoleAssignmentQueryParams
} from '../../models/aadPAP/roleAssignment/IRoleAssignmentQueryParams';
import { ISelectedRoleAssignment } from '../../models/aadPAP/roleAssignment/ISelectedRoleAssignment';
import { IRoleDefinition } from '../../models/aadPAP/roleDefinition/IRoleDefinition';
import { IGraphObjectInfo } from '../../models/graph';
import {
  STYLES_FILTER_DETAILS_LIST,
  STYLES_SEARCH_ICON_DETAILS_LIST
} from '../../Styles/STYLES_FILTER_QUERY_LIST_RAS';
import { isGuid } from '../../utilities/guid';
import { deepClone } from '../../utilities/object';
import { ConfirmationDialog } from '../Common/DeleteConfirmationDialog/ConfirmationDialog';
import { FormPanelType } from '../RoleDefinition/RoleDefinitionPanel/RoleDefinitionPanel.type';
import ModalSearchObjects from '../SearchObjects/ModalSearchOjects/ModalSearchObjects';
import { getRoleAssignmentCommandBarItems } from './RoleAssignment.commands';
import {
  IRoleAssignmentProps,
  IRoleAssignmentState
} from './RoleAssignment.types';
import { ConnectedRoleAssignmentPanel } from './RoleAssignmentPanel/ConnectedRoleAssignmentPanel';
import { ConnectedRoleAssignmentsTable } from './RoleAssignmentsTable/ConnectedRoleAssignmentsTable';

const detailsPageStyles = require('@microsoft/portal-app/lib/styling/patterns/DetailsPage.scoped.scss');
const listPageStyles = require('@microsoft/portal-app/lib/styling/patterns/ListPage.scoped.scss');
const pageLayoutStyles = require('../../Styles/PageLayout.scoped.scss');

/**
 * TODO: Describe what the RoleAssignment control represents.
 */
export class RoleAssignment extends React.Component<
  IRoleAssignmentProps,
  IRoleAssignmentState
> {
  // tslint:disable-next-line:no-any
  public tableRef = React.createRef<any>();
  private pageFocusRef = React.createRef<HTMLDivElement>();
  private initState: IRoleAssignmentState = {
    textFilter: '',
    optionRDs: [],
    selOptKeyRD: -1,
    strQueryObjID: '',
    showModal: false,
    curObj: {} as IGraphObjectInfo,
    showPanel: false,
    panelType: FormPanelType.view,
    selectedRoleAssignment: {} as ISelectedRoleAssignment,
    showDeleteConfirmModal: false
  };

  constructor(props: IRoleAssignmentProps) {
    super(props);
    this.state = { ...this.initState };
  }

  public componentWillMount = () => {
    if (
      this.props.listRoleAssignments.currentAppId !==
      this.props.currentNpd.appId
    ) {
      let queryRA = { ...EmptyRoleAssignmentQueryParams };
      queryRA.appId = this.props.currentNpd.appId;
      this.props.getListRAs(queryRA);
      return;
    }

    let selOptKeyRD: string | number = -1;
    let strQueryObjID: string = '';
    if (
      !isNullOrUndefined(this.props.roleAssignmentQueryParams.filterSearchRA)
    ) {
      if (
        this.props.roleAssignmentQueryParams.filterSearchRA.principalId !== null
      ) {
        strQueryObjID = this.props.roleAssignmentQueryParams.filterSearchRA
          .principalId;
      }
      if (
        this.props.roleAssignmentQueryParams.filterSearchRA.roleDefinitionId !==
        null
      ) {
        selOptKeyRD = this.props.roleAssignmentQueryParams.filterSearchRA
          .roleDefinitionId;
      }
    }

    this.setState({
      selOptKeyRD: selOptKeyRD,
      strQueryObjID: strQueryObjID
    });
  }

  public componentDidMount = () => {
    this._getDropDownItems(this.props.listRoleDefinitions.roleDefinitions);
    this._focus();
  }

  public componentWillReceiveProps(nextProps: IRoleAssignmentProps) {
    if (this.props.currentNpd.appId !== nextProps.currentNpd.appId) {
      this.setState({ ...this.initState });
      const queryRA: IRoleAssignmentQueryParams = {
        appId: nextProps.currentNpd.appId,
        filterSearchRA: {
          principalId: null,
          roleDefinitionId: null
        }
      };
      this.props.getListRAs(queryRA);
    }

    if (
      this.props.listRoleDefinitions.isLoading &&
      !nextProps.listRoleDefinitions.isLoading &&
      !isNullOrUndefined(nextProps.listRoleDefinitions.roleDefinitions)
    ) {
      this._getDropDownItems(nextProps.listRoleDefinitions.roleDefinitions);
    }
  }

  public render(): JSX.Element {
    const { t } = this.props;

    const { selectedRoleAssignments } = this.props.listRoleAssignments;
    let { bulkDeletionComplete } = this.props.listRoleAssignments;
    if (bulkDeletionComplete === undefined) {
      bulkDeletionComplete = true;
    }
    const commands = getRoleAssignmentCommandBarItems(
      this.refresh,
      this._setShowPanel,
      this._showDialog,
      // TODO use reselect
      !(
        selectedRoleAssignments &&
        selectedRoleAssignments.length > 0 &&
        bulkDeletionComplete
      )
    );
    return (
      <div className={css('ms-DetailsPage', listPageStyles.listPage)}>
        <div
          className={css(
            'ms-DetailsHeader',
            listPageStyles.pageHeader,
            pageLayoutStyles.pageHeader
          )}
        >
          <div
            role="heading"
            tabIndex={-1}
            aria-level={1}
            ref={this.pageFocusRef}
            className={css(
              'ms-PageTitle',
              listPageStyles.pageTitle,
              listPageStyles.pageSectionTitle
            )}
          >
            {t('Role Assignment')}
          </div>
        </div>
        <CommandBar
          className={css(pageLayoutStyles.commandBar)}
          items={commands}
          ariaLabel={this.props.t('Role Assignment commands')}
        />
        <div
          className={css(
            'ms-DetailsContent',
            'ms-Grid',
            detailsPageStyles.detailsContent
          )}
        >
          <div className={css('Search-Bar-Row', 'ms-Grid-row')}>
            <div className={css('Search-Bar', pageLayoutStyles.msGridCol4)}>
              <TextField
                onChange={this.onFilter}
                styles={STYLES_FILTER_DETAILS_LIST}
                value={this.state.textFilter}
                placeholder="Filter by Role Definition Name, Display Name or Scope"
                ariaLabel="Filter by Role Definition Name, Display Name or Scope"
              />
            </div>
            <div className={css('Search-Bar', pageLayoutStyles.msGridCol4)}>
              <Dropdown
                selectedKey={this.state.selOptKeyRD}
                placeholder="Search by Role Definition"
                ariaLabel="Filter by Role Definition"
                options={this.state.optionRDs}
                onChange={this.searchByRD}
              />
            </div>
            <div className={css('Search-Bar', pageLayoutStyles.msGridCol4)}>
              <TextField
                iconProps={{ iconName: 'Search' }}
                value={this.state.strQueryObjID}
                onChange={this.onChangeSearchObjId}
                placeholder="Search by Principal ID ..."
                ariaLabel="Search by Principal ID"
              />
              <IconButton
                styles={STYLES_SEARCH_ICON_DETAILS_LIST}
                iconProps={{ iconName: 'Search' }}
                onClick={this.openSearchObjModal}
                title="Search Object"
                ariaLabel="Search Object"
              />
            </div>
          </div>
          <div className={css('Table-Row', 'ms-Grid-row')}>
            <ConnectedRoleAssignmentsTable
              setShowPanel={this._setShowPanel}
              textFilter={this.state.textFilter}
              ref={this.tableRef}
            />
          </div>
          <ConnectedRoleAssignmentPanel
            showPanel={this.state.showPanel}
            roleAssignmentPanelType={this.state.panelType}
            onDismiss={this._hidePanel}
          />
          {this.state.showDeleteConfirmModal && (
            <ConfirmationDialog
              cancel={this._closeDialog}
              confirm={this._bulkDeleteRAs}
              isHidden={!this.state.showDeleteConfirmModal}
              title={t('Delete Record(s)')}
              subText={t('Selected Record(s) will be deleted.')}
              confirmBtnText={t('Delete')}
              cancelBtnTxt={t('Cancel')}
              t={t}
            />
          )}
          <ModalSearchObjects
            showModal={this.state.showModal}
            selectObject={this.selectObject}
            t={this.props.t}
          />
        </div>
      </div>
    );
  }

  private _focus = () => {
    const node = this.pageFocusRef.current;
    if (node) {
      node.focus();
    }
  }

  private _getDropDownItems = (list: IRoleDefinition[]) => {
    if (!isNullOrUndefined(list) && list !== [] && list.length !== 0) {
      let optionRDs: IDropdownOption[] = [
        { key: '', text: '- All Role Definitions -' } as IDropdownOption
      ];
      for (let i = 0; i < list!.length; i++) {
        optionRDs.push({
          data: list[i],
          key: list[i].id,
          text: list[i].name
        } as IDropdownOption);
      }
      this.setState({
        optionRDs: optionRDs
      });
    }
  }

  private refresh = () => {
    if (!this.tableRef.current) {
      return;
    }
    this.tableRef
      .current!.getWrappedInstance()
      .getWrappedInstance()
      .refresh();
    this.setState({
      textFilter: '',
      selOptKeyRD: -1,
      strQueryObjID: ''
    });
  }

  private onFilter = (
    _ev: React.FormEvent<HTMLInputElement | HTMLTextAreaElement>,
    text: string | undefined
  ): void => {
    this.setState({
      textFilter: text ? text : ''
    });
  }

  private searchByRD = (
    _event: React.FormEvent<HTMLDivElement>,
    option?: IDropdownOption,
    _index?: number
  ): void => {
    let roleAssignmentQueryParams = deepClone(
      this.props.roleAssignmentQueryParams
    );
    roleAssignmentQueryParams.skipToken = undefined;
    if (option!.key === '') {
      roleAssignmentQueryParams.filterSearchRA.roleDefinitionId = null;
      this.setState({
        selOptKeyRD: -1
      });
    } else {
      roleAssignmentQueryParams.filterSearchRA.roleDefinitionId = (option!
        .data as IRoleDefinition).id;
      this.setState({
        selOptKeyRD: option!.key
      });
    }

    if (
      this.props.roleAssignmentQueryParams.filterSearchRA.roleDefinitionId !==
      roleAssignmentQueryParams.filterSearchRA.roleDefinitionId
    ) {
      this.props.getListRAs(roleAssignmentQueryParams);
    }
  }
  private _bulkDeleteRAs = () => {
    this.props.deleteSelectedRoleAssignments();
    this._closeDialog();
  }
  private _showDialog = (): void => {
    this.setState({ showDeleteConfirmModal: true });
  }

  private _closeDialog = (): void => {
    this.setState({ showDeleteConfirmModal: false });
  }
  private openSearchObjModal = () => {
    this.setState({
      showModal: true
    });
  }

  private onChangeSearchObjId = (
    _ev: React.FormEvent<HTMLInputElement | HTMLTextAreaElement>,
    text: string | undefined
  ): void => {
    if (isNullOrUndefined(text)) {
      return;
    }

    let objId: string | null = text;
    if (!isGuid(text) && text !== '') {
      return;
    } else if (text === '') {
      objId = null;
    }

    this.searchByObjId(objId);
  }

  private selectObject = (obj?: IGraphObjectInfo) => {
    this.setState({
      showModal: false
    });
    if (isNullOrUndefined(obj)) {
      return;
    }
    this.searchByObjId(obj.objectId);
  }

  private searchByObjId = (objId: string | null) => {
    let roleAssignmentQueryParams = { ...this.props.roleAssignmentQueryParams };
    roleAssignmentQueryParams.skipToken = undefined;
    roleAssignmentQueryParams.filterSearchRA.principalId = objId;
    this.setState({
      strQueryObjID: objId ? objId : ''
    });
    this.props.getListRAs(roleAssignmentQueryParams);
  }

  private _setShowPanel = (showPanel: boolean, panelType: FormPanelType) => {
    this.setState({ showPanel: showPanel, panelType: panelType });
  }

  private _hidePanel = (): void => {
    // if (!this.state.showPanel) {
    //   this._selection.toggleIndexSelected(this._selection!.getSelectedIndices()[0]);
    // }
    this.setState({
      showPanel: false,
      selectedRoleAssignment: {} as ISelectedRoleAssignment
    });
  }
}



