import { classNamesFunction, IStyle } from 'office-ui-fabric-react';

export type IDeleteConfirmationDialogStyleProps = {};

export interface IDeleteConfirmationDialogStyles {
  large?: IStyle;
  small?: IStyle;
}

export function getStyles(
  _props: IDeleteConfirmationDialogStyleProps
): IDeleteConfirmationDialogStyles {
  return {
    large: [
      {
        selectors: {
          '.ms-Dialog-main': {
            width: 330,
            height: 315
          },
          '.ms-Dialog-title': {
            paddingTop: 49,
            fontWeight: 300,
            paddingLeft: 39
          },
          '.ms-Dialog-button--close': {
            width: 16,
            height: 16,
            paddingTop: 12,
            paddingRight: 12
          },
          '.ms-Dialog-inner': {
            paddingLeft: 39
          },
          '.ms-Dialog-content': {
            height: 132
          },
          '.ms-Dialog-actions': {
            marginTop: 23
          },
          '.ms-Dialog-actionsRight': {
            textAlign: 'left'
          }
        }
      }
    ],
    small: []
  };
}
const getClassNames = classNamesFunction<
  IDeleteConfirmationDialogStyleProps,
  IDeleteConfirmationDialogStyles
>();
export const dialogClass = getClassNames(getStyles, {});



