import { createAction } from '@microsoft/portal-app/lib/redux/createAction';
import { translate } from 'react-i18next';
import { connect, Dispatch } from 'react-redux';
import { AuthzActions, IRootAuthzState } from '../../models';
import { IRoleAssignmentQueryParams } from '../../models/aadPAP/roleAssignment/IRoleAssignmentQueryParams';
import { RoleAssignment } from './RoleAssignment';
import {
  IConnectedRoleAssignmentProps,
  IRoleAssignmentActions,
  IRoleAssignmentMappedProps
} from './RoleAssignment.types';

/**
 * Maps the external (i.e. own) props and the state to the properties of the RoleAssignment control.
 * @param state The current redux state to map to props
 * @param ownProps The external properties to map to props
 * @returns The mapped properties that along with the actions
 * becomes the props (IRoleAssignmentProps) for the RoleAssignment control
 */
const mapStateToProps = (
  state: IRootAuthzState,
  _ownProps: IConnectedRoleAssignmentProps
): IRoleAssignmentMappedProps => {
  const {
    currentNpd,
    listRoleDefinitions,
    listRoleAssignments,
    roleAssignmentQueryParams
  } = state.app;
  return {
    currentNpd,
    listRoleDefinitions,
    listRoleAssignments,
    roleAssignmentQueryParams
  };
};

/**
 * Maps the dispatch actions to the props for the RoleAssignment control.
 * @param dispatch Dispatches actions to the store
 * @returns The mapped actions that along with the mapped properties
 * becomes the props (IRoleAssignmentProps) for the RoleAssignment control
 */
const mapDispatchToProps = (
  dispatch: Dispatch<IRootAuthzState>
): IRoleAssignmentActions => {
  return {
    getListRAs: createAction<IRoleAssignmentQueryParams>(
      AuthzActions.getRoleAssignmentList,
      dispatch
    ),
    updateRoleAssignmentListQuery: createAction<IRoleAssignmentQueryParams>(
      AuthzActions.updateRoleAssignmentListQuery,
      dispatch
    ),
    deleteSelectedRoleAssignments: createAction(
      AuthzActions.bulkDeleteRoleAssignments,
      dispatch
    )
  };
};

/**
 * A redux connected RoleAssignment control.
 * @description Supports translation
 */
export const ConnectedRoleAssignment = connect(
  mapStateToProps,
  mapDispatchToProps
)(translate('App')(RoleAssignment));



